import { Component, OnInit } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-property-facility',
  templateUrl: './property-facility.component.html',
  styleUrls: ['./property-facility.component.scss']
})
export class PropertyFacilityComponent implements OnInit {
  
  isDataLoaded:boolean = false;
  propertyTowerData:any;
  facilityData:any;

  dotItems = ['high','medium','low','blue','cyan'];

  constructor(
  	private apartmentService:ApartmentService,
    private facilityService:FacilityService,
    private cookieService:CookieService
  ) { }

  isFacilityEmpty(){
    return this.facilityData.length == 0
  }

  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {

      this.propertyTowerData = res;
      
      let params = {
        ApartmentID: parseInt(this.cookieService.get('apartmentId'))
      }

      this.facilityService.getPcApartmentFacility(params).subscribe((res:any) => {

          this.facilityData = res;

          _.each(this.facilityData, (item, index) => {
            this.facilityData[index].class = this.dotItems[Math.floor(Math.random() * this.dotItems.length)];
          });

          this.isDataLoaded = true;

      });

  	});

  }

}
