import { Component, OnInit } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-property-profile',
  templateUrl: './property-profile.component.html',
  styleUrls: ['./property-profile.component.scss']
})
export class PropertyProfileComponent implements OnInit {
  
  isDataLoaded:boolean = false;
  isEdit:boolean = false;

  towersLength:any;
  unitsLength:any;

  property:any;

  propertyTypeData:any;
  propertyCategoryData:any;

  isFileAdded:boolean = false;
  selectedFile: File;
  fileList: File[] = [];
  listOfFiles: any[] = [];

  constructor(
	private apartmentService:ApartmentService,
	private lookupService: LookupService,
	private sharedService:SharedService,
	private cookieService:CookieService
  ) { }

  

  getProperty(event){
    
    
    this.property = event;
    this.updateApartment( res => {
      var params = {
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        active: 2
      }
  
      this.apartmentService.getApartmentByApartmentId(params).subscribe((res:any) => {
  
        this.property  = res[0];
  
      });
    });

    
    
  }

  
  getPropertyType(id){

  	var data = _.filter( this.propertyTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  getPropertyCategory(id){

  	var data = _.filter( this.propertyCategoryData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  getDate(date){
  	if(date === null || date === undefined || date.length == 0) {
  		return "NA"
  	}
  	else {
  		return moment(date).format("DD-MM-YYYY");
  	}
    
  }

  showEditForm(){
  	this.isEdit = !this.isEdit;
  }

  submitProfileBasicForm(form) {

  	this.isDataLoaded = false;
    this.updateApartment(res => {

      

    });
  }

  updateApartment(callback) {
    
    var details = {
	  
  	  "apartmentId": parseInt(this.cookieService.get('apartmentId')),
  	  "apartmentName": this.property.apartmentName,
  	  "description": this.property.description,
  	  "builderName": this.property.builderName,
  	  "registrationNumber": this.property.registrationNumber,
  	  "emailId": this.property.emailId,
  	  "phoneNumber": this.property.phoneNumber,
  	  "address1": this.property.address1,
  	  "address2": this.property.address2,
  	  "city": this.property.city,
  	  "state": this.property.state,
  	  "pincode": this.property.pincode,
  	  "landmark": this.property.landmark,
  	  "logoImageId": 0,
  	  "isActive": true,
  	  "insertedBy": 0,
  	  "insertedOn": "2020-02-06T16:14:17.670Z",
  	  "updatedBy": parseInt(this.cookieService.get('userId')),
  	  "updatedOn": new Date().toISOString(),
  	  "updatePersonName": this.property.updatePersonName,
  	  "insertPersonName": this.property.insertPersonName,
  	  "location": this.property.location,
  	  "constructionStartDate": this.property.constructionStartDate,
  	  "constructionEndDate": this.property.constructionEndDate,
  	  "occupanyDate": this.property.occupanyDate,
  	  "propertyTypeId": parseInt(this.property.propertyTypeId),
  	  "propertyCategoryId": parseInt(this.property.propertyCategoryId),
  	  "imageId":  this.property.imageId,
  	  "propertyTypeName": this.property.propertyTypeName,
  	  "propertyCategoryName": this.property.propertyCategoryName,
  	  "fileName": null,
  	  "filePath": null,
  	  "commonSqMt": this.property.commonSqMt,
  	  "totalSqMt": this.property.totalSqMt
	  }

    var apartmentParams = {
      file: null,
      apartment: details
    }


    this.apartmentService.updateApartment(apartmentParams).subscribe((res:any) => {

      if(res.message) {
        this.isDataLoaded = true;
        this.isEdit = false;
        this.sharedService.setAlertMessage("Property updated successfully");
      }
      else {
        this.isDataLoaded = true;
        this.isEdit = false;
      }

      callback();

    })
  }

  ngOnInit() {

  	this.property = {};
    this.property.propertyTypeId = "";
    this.property.propertyCategoryId = "";

  	var params = {
  		apartmentId: parseInt(this.cookieService.get('apartmentId')),
  		active: 2
  	}

  	this.apartmentService.getApartmentByApartmentId(params).subscribe((res:any) => {

      this.property  = res[0];
      
      let params = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }

  		this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {

        this.towersLength = res.length;
        
        let params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId'))
        }

  			this.apartmentService.getApartmentBlockUnitByApartmentId(params).subscribe((res:any) => {
  				this.unitsLength = res.length;
  			});

  		});

  		this.isDataLoaded = true;

  	});

    let propertyParams = {
      LookupTypeId: 65
    }

  	//get property type
    this.lookupService.getLookupValueByLookupTypeId(propertyParams).subscribe((res:any) => {
		this.propertyTypeData = res;
    },
    error => {
      
    });

    let propertyCategoryParams = {
      LookupTypeId: 66
    }

  	//get property category
    this.lookupService.getLookupValueByLookupTypeId(propertyCategoryParams).subscribe((res:any) => {
		this.propertyCategoryData = res;
    },
    error => {
      
    });

  }

}
