import { Component, OnInit } from '@angular/core';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-property-tower-list',
  templateUrl: './property-tower-list.component.html',
  styleUrls: ['./property-tower-list.component.scss']
})
export class PropertyTowerListComponent implements OnInit {

  propertyTowerData:any;
  constructor(//private srvPV: PropertyViewService,
    private apartmentService:ApartmentService,
    private cookieService:CookieService) { }

  ngOnInit(): void {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.apartmentService.getApartmentBlockByApartmentId(params).subscribe((res:any) => {
      console.log('Tower Details');
      console.log(res);
  		this.propertyTowerData = res;

  	});
  }
  GetDetails(item){
    console.log(item);
   // this.srvPV.Mode.next({ mode: 'Details',data:item });
  }

}
