import { Component, OnInit, AfterViewInit, HostListener  } from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';

import * as _ from 'underscore';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-property-configuration',
  templateUrl: './property-configuration.component.html',
  styleUrls: ['./property-configuration.component.scss']
})


export class PropertyConfigurationComponent implements OnInit, AfterViewInit {

  itemPositions:any = [];
  menuList:Array<any>=[];
  currentPosition:any = 0;
  openCollapse=true;
  totalTabItems:any;
  isShowMainRoute=true;
  
  tab:any;
  tabItems:any;

  assetCategoriesData:any;
  isRouteContainer:boolean=false;

  isDataLoaded:boolean = false;
  isShowSubCategory:boolean=false
  categoryList:Array<any>=[];
  subMenuList:Array<any>=[];
  selectedCategory:any;
  selectedCategoryName = '';
  selectedSubCategoryName = ''

  constructor(
    private lookupService: LookupService,private router : Router,private cookieService : CookieService
  ) { }


  moveTabRight(){
  	if (this.currentPosition + 1 < this.itemPositions.length) {
      document.getElementById("property-tabs").style.left = "-" + this.itemPositions[++this.currentPosition] + "px";
  	}
    
  }

  moveTabLeft(){
    if (this.currentPosition - 1 >= 0) {
      document.getElementById("property-tabs").style.left = "-" + this.itemPositions[--this.currentPosition] + "px";
    }
  }

  widthOfList() {

    this.itemPositions = [];
  	var marginWidth = 60;

  	var wrapperWidth = document.querySelector('#property-wrapper').clientWidth - marginWidth;

  	var itemsWidth = 0;
  	this.itemPositions.push(0);

  	_.each(this.tabItems, (item)=> {
  		var itemWidth = item.offsetWidth;
  	    itemsWidth += itemWidth;
  	    if (itemsWidth > wrapperWidth) {
  	      this.itemPositions.push( itemsWidth - wrapperWidth ); 
  	    }
  	});

  	return itemsWidth;

  }


  isMobileView() {
    return window.innerWidth <= 576 ? true : false;
  }

  @HostListener('window:resize', ['$event'])
  onWindowResize(event) {
    this.widthOfList();
    if(this.isMobileView()){
      document.getElementById("property-tabs").style.left = 0 + 'px';
      this.currentPosition = 0;
    }
  }

  ngOnInit() {
    this.menuList=[
      {
        name:'Property Profile',
        url:'/profile',
        bg:'rgb(63, 81, 181)',
        isActive:true
      },
      {
        name:'Property Details',
        url:'/details',
        bg:'rgb(0, 150, 136)',
        isActive:false
      },
      {
        name:'Unit Facility',
        url:'/facility',
        bg:'rgb(255, 152, 0)',
        isActive:false
      },
      {
        name:'Parking Details',
        url:'/parking',
        bg:'rgb(255, 87, 34)',
        isActive:false
      },
      {
        name:'Assets',
        url:'asset',
        bg:'rgb(96, 125, 139)',
        isActive:false
      }
    ];
    this.getAllCategory();
  }

  getAllCategory() {
		let queryParamBase:any={};
       queryParamBase = {
      apartmentId: this.cookieService.get('apartmentId'),
      lookupTypeId: 19,
      subCategoryLookupTypeId: 68,
    };
		this.lookupService.getSubcategory(queryParamBase).subscribe((res: any) => {
			if (res) {
        this.categoryList = res ? res : [];
        this.categoryList.filter(key =>{
          key['bg'] = this.getRandomColor();
        })
        this.subMenuList = this.categoryList[0].subCategory;
			}
		});
  }
  
  sortSubCategory(menu){
    this.selectedCategory = menu.id;
    this.selectedCategoryName = menu.name;
    this.selectedSubCategoryName = '';
    this.categoryList.filter(key =>{
      if(key.id == menu.id){
        this.subMenuList = key.subCategory;
      }
    })
  }

  getRandomColor() {
    var letters = '0123456789ABCDEF';
    var color = '#';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  navigateProperty(menu){
    if(menu.name == 'Assets'){
      this.isShowSubCategory = true;
      this.isShowMainRoute = false;
    }else{
      this.isShowSubCategory = false;
      this.selectedCategoryName = '';
      this.selectedSubCategoryName = '';
      let rootUrl = 'ams/my-property/property-configuration'+menu.url;
      this.router.navigateByUrl(rootUrl);
    }
  }

  ngAfterViewInit() {

    let params = {
      LookupTypeId: 19
    }

    //get all asset categories
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {

      this.isDataLoaded = true;

      this.assetCategoriesData = res.filter(item => {
        return item.isActive;
      });
      
      setTimeout(() => {

        this.tab = document.getElementById('property-tabs');
        this.tabItems = this.tab.getElementsByTagName("li");
        this.totalTabItems = this.tabItems.length;

        this.widthOfList();

      }, 1000);


    });

  



  }

}
