import { Component, ElementRef, ViewChild,OnInit, ViewEncapsulation } from '@angular/core';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { CookieService } from 'ngx-cookie-service';
import html2canvas from 'html2canvas';
import * as jsPDF from 'jspdf';



@Component({
  selector: 'app-property-view-grid',
  templateUrl: './property-view-grid.component.html',
  styleUrls: ['./property-view-grid.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PropertyViewGridComponent implements OnInit {

  public towerList:Array<any>=[];
  public filterList:Array<any>=[];
  public floorData:Array<any>=[];
  public selectedBlockId:number;
  public selectedTowerName:string;
  @ViewChild('scene', { static: false }) scene;
  @ViewChild('propertyView') propertyView: ElementRef;
  panZoomController;
  zoomLevels: number[];

  currentZoomLevel: number;
  public scaleIndexValue=1;
  public scaleIndex = `scale(${this.scaleIndexValue})`;
  public filterCategory:Array<any>=[];
  public selectedFilter="";
  public customOptions: OwlOptions = {
    // loop: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    // margin:15,
    dots: false,
    navSpeed: 700,
    navText: ["<i class='fa fa-chevron-left'></i>", "<i class='fa fa-chevron-right'></i>"],
    responsive: {  
      0: {
        items: 2
      },
      400: {
        items: 2
      },
      740: {
        items: 3
      },
      940: {
        items: 4
      }
    },
    nav: true
  }

  constructor(private apartmentService : ApartmentService,
              private cookieService : CookieService) { }

  ngOnInit(): void {
    this.filterList=[
      {
        color:"#9c27b0",
        name:"1BHK Units",
        count:0
      },
      {
        color:"#2196f3",
        name:"2BHK Units",
        count:0
      },
      {
        color:"#ff558f",
        name:"3BHK Units",
        count:0
      },
      // {
      //   color:"#3bbe9e",
      //   name:"Party Hall Units",
      //   count:100
      // }
    ]
    this.filterCategory=[
      {
        name:'Unit Status- Vacant,Sale,Occupied,Construction',
        id:'isVacant',
        isClubbed:true,
        color:'#3777ce'
      },
      {
        name:'Unit Resident type- Owner/Tenant',
        id:'isAvailableForRent',
        isClubbed:true,
        color:'#e91e63'
      },
      {
        name:'Unit Size Type 1BHK, 2BHK, 3BHK',
        id:'lookupValueName',
        isClubbed:true,
        typeList:[
          {
            type:'1BHK',
            color:'#9c27b0'
          },
          {
            type:'2 BR',
            color:'#2196f3'
          },
          {
            type:'3BHK',
            color:'#ff558f'
          }
        ]
      },
      {
        name:'Units with Parking Status',
        id:'',
        isClubbed:false,
        color:'#2196f3'
      },
      {
        name:'Units with Pets',
        id:'isPetsAllowed',
        isClubbed:false,
        color:'#009688'
      },
      {
        name:'Units with Elderly residents (Age 60+)',
        id:'',
        isClubbed:false,
        color:'#8bc34a'
      },
      {
        name:'Units with Kids',
        id:'',
        isClubbed:false,
        color:'#ffc107'
      },
      {
        name:'Units with rental Agreement expiring in 30 days',
        id:'',
        isClubbed:false,
        color:'#795548'
      },
      {
        name:'Units with moveout scheduled in 7 days',
        id:'',
        isClubbed:false,
        color:'#607d8b'
      },
      {
        name:'Units with outofcondo Status',
        id:'isActive',
        isClubbed:false,
        color:'#f44336'
      }
    ]
    this.getBlockList();
  }

  getSelectedUnits(category,unitObj){
    console.log(unitObj[category])
    if(category != '' && unitObj[category]){
      let selectedCategoryObj = this.filterCategory.find(key => {if(key.id == category) return key})
      if(selectedCategoryObj.isClubbed){
        let selectedColor = selectedCategoryObj.typeList.filter(item =>{
          if(item.type == unitObj[category]){
            return item.color;
          }
        });
        return selectedColor[0].color;
      }else{
        return selectedCategoryObj.color;
      }
    }
  }

  getBlockList(){
    let param ={
      apartmentId : parseInt(this.cookieService.get('apartmentId'))
    }
    this.apartmentService.getApartmentBlockByApartmentId(param).subscribe(
      (resp:any) =>{
        this.towerList = resp;
        this.selectedBlockId = this.towerList[0].apartmentBlockId;
        this.selectedTowerName = this.towerList[0].description;
        this.getTowerDetails();
      },
      error =>{

      }
    )
  }

  getTowerDetails(){
    let param ={
      apartmentId:parseInt(this.cookieService.get('apartmentId')),
      apartmentBlockId:this.selectedBlockId
    };
    this.apartmentService.getViewUnitTypeByApartmentId(param).subscribe(
      (resp:any) =>{
        this.floorData = resp;
        this.getTotalCount();
      },
      error =>{

      }
    )
  }

  getTotalCount(){
    if(this.floorData && this.floorData.length){
      this.floorData.filter(key =>{
        if(key.lookupValueName == '1BHK'){
          this.filterList[0].count += 1;
        }else if(key.lookupValueName == '2BHK'){
          this.filterList[1].count += 1;
        }else if(key.lookupValueName == '3BHK'){
          this.filterList[2].count += 1;
        }
      })
    }
  }

  selectedTower(blockId,name){
    this.scaleIndexValue = 1 ;
    this.scaleIndex = `scale(${this.scaleIndexValue})`;
    this.selectedFilter = "";
    this.selectedBlockId = blockId;
    this.selectedTowerName = name;
    this.getTowerDetails();
  }

  downloadPdf(){
    setTimeout(() =>{
      var data = document.getElementById('property-view');  
    html2canvas(data).then(canvas => {  
      // Few necessary setting options  
      var imgWidth = 208;   
      // var pageHeight = 295;    
      var imgHeight = canvas.height * imgWidth / canvas.width;  
      // var heightLeft = imgHeight;  
      
      const contentDataURL = canvas.toDataURL('image/png')  
      let pdf = new jsPDF('p', 'mm', 'a4'); // A4 size page of PDF  
      var position = 0;  
      pdf.addImage(contentDataURL, 'PNG', 0, position, imgWidth, imgHeight)  
      pdf.save(`${this.selectedTowerName}.pdf`);
    });  
  },1000);
  }

  zoomIn(){
    if(this.scaleIndexValue <= 2.5){
      this.scaleIndexValue += 0.5;
      this.scaleIndex = `scale(${this.scaleIndexValue})`;
    }
  }
  zoomOut(){
    if(this.scaleIndexValue >= 1.5){
    this.scaleIndexValue -= 0.5;
    this.scaleIndex = `scale(${this.scaleIndexValue})`;
  }
}

}
